<template>
    <div class="sidebar">
        <el-menu
            class="sidebar-el-menu"
            :default-active="onRoutes"
            :collapse="collapse"
            background-color="#324157"
            text-color="#bfcbd9"
            active-text-color="#20a0ff"
            unique-opened
            router
        >
            <template v-for="item in items">
                <template v-if="item.subs">
                    <el-submenu :index="item.index" :key="item.index">
                        <template slot="title">
                            <i :class="item.icon"></i>
                            <span slot="title">{{ item.title }}</span>
                        </template>
                        <template v-for="subItem in item.subs">
                            <el-submenu
                                v-if="subItem.subs"
                                :index="subItem.index"
                                :key="subItem.index"
                            >
                                <template slot="title">{{ subItem.title }}</template>
                                <el-menu-item
                                    v-for="(threeItem,i) in subItem.subs"
                                    :key="i"
                                    :index="threeItem.index"
                                >{{ threeItem.title }}</el-menu-item>
                            </el-submenu>
                            <el-menu-item
                                v-else
                                :index="subItem.index"
                                :key="subItem.index"
                            >{{ subItem.title }}</el-menu-item>
                        </template>
                    </el-submenu>
                </template>
                <template v-else>
                    <el-menu-item :index="item.index" :key="item.index">
                        <i :class="item.icon"></i>
                        <span slot="title">{{ item.title }}</span>
                    </el-menu-item>
                </template>
            </template>
        </el-menu>
    </div>
</template>

<script>
import bus from '../common/bus';
export default {
    data() {
        return {
            collapse: false,
            items: [
                {
                    icon: 'el-icon-lx-home',
                    index: 'dashboard',
                    title: '系统首页'
                },
                {
                    icon: 'el-icon-picture-outline',
                    index: '1',
                    title: '图像技术',
                    subs: [
                        {
                            index: '1-1',
                            title: '图片效果增强',
                            subs: [
                                {
                                    index: 'image_technology_image_enhancement_index1',
                                      title: '图像去雾',
                                      icon: 'el-icon-picture-outline'
                                  },
                                  {
                                    index: 'image_technology_image_enhancement_index2',
                                      title: '图像对比度增强',
                                      icon: 'el-icon-picture-outline'
                                  },
                                  {
                                    index: 'image_technology_image_enhancement_index3',
                                      title: '图像无损放大',
                                      icon: 'el-icon-picture-outline'
                                  },
                                  {
                                    index: 'image_technology_image_enhancement_index4',
                                      title: '黑白图像上色',
                                      icon: 'el-icon-picture-outline'
                                  },
                                  {
                                    index: 'image_technology_image_enhancement_index5',
                                      title: '拉升图像恢复',
                                      icon: 'el-icon-picture-outline'
                                  },
                                  {
                                    index: 'image_technology_image_enhancement_index6',
                                      title: '图像风格转换',
                                      icon: 'el-icon-picture-outline'
                                  },
                                  {
                                    index: 'image_technology_image_enhancement_index7',
                                      title: '图像清晰度增强',
                                      icon: 'el-icon-picture-outline'
                                  },
                                  {
                                    index: 'image_technology_image_enhancement_index8',
                                      title: '人像动漫画',
                                      icon: 'el-icon-picture-outline'
                                  },
                                  {
                                    index: 'image_technology_image_enhancement_index9',
                                      title: '图片修复',
                                      icon: 'el-icon-picture-outline'
                                  }
                            ]
                        },
                    ]
                },
            ]
        };
    },
    computed: {
        onRoutes() {
            return this.$route.path.replace('/', '');
        }
    },
    created() {
        // 通过 Event Bus 进行组件间通信，来折叠侧边栏
        bus.$on('collapse', msg => {
            this.collapse = msg;
            bus.$emit('collapse-content', msg);
        });
    }
};
</script>

<style scoped>
.sidebar {
    display: block;
    position: absolute;
    left: 0;
    top: 50px;
    bottom: 0;
    overflow-y: scroll;
}
.sidebar::-webkit-scrollbar {
    width: 0;
}
.sidebar-el-menu:not(.el-menu--collapse) {
    width: 250px;
}
.sidebar > ul {
    height: 100%;
}
</style>
